import React from 'react'
import { Typography, Table, Button } from "antd";
import {firstButtonStyle, columns, dataSource} from "../../common/invitees";

const { Title } = Typography;

const FinalizedView = () => {
    return (
        <>
            <Title level={4}>Event Name, Time, @ Location</Title>
            <Title level={4}>Invitees</Title>
            <Table dataSource={dataSource} columns={columns} />
            <Button type="primary" style={firstButtonStyle}>
                Accept
            </Button>
            <Button type="default">Decline</Button>
        </>
    );
};

export default FinalizedView
