import React from "react";
import { Checkbox, List } from "antd";
import { TicketmasterEvent } from "../../common/types";
import { Dispatch } from "react";
import { SetStateAction } from "react";
import { CheckboxChangeEvent } from "antd/lib/checkbox";

export const formatDate = (dateString: string) => {
    const options: Intl.DateTimeFormatOptions = {
        year: "numeric",
        month: "long",
        day: "numeric",
        hour: "numeric",
        minute: "numeric",
    };
    return new Date(dateString).toLocaleDateString(undefined, options);
};

type EventsListProps = {
    events: TicketmasterEvent[];
    selectedEvents: TicketmasterEvent[];
    onChange: (e: CheckboxChangeEvent, index: number) => void;
};

const EventsList: React.FC<EventsListProps> = ({
    events,
    onChange,
    selectedEvents,
}) => {
    return (
        <List
            itemLayout="horizontal"
            dataSource={events}
            style={{
                marginLeft: "3em",
                width: 500,
            }}
            renderItem={(
                { name, localDate, localTime, venueAddress, url },
                index
            ) => (
                <List.Item
                    style={{
                        padding: 12,
                    }}
                >
                    <>
                        <Checkbox
                            style={{
                                marginRight: 12,
                            }}
                            // onChange={() => onChange?.(index)}
                            onChange={(e) => onChange(e, index)}
                            checked={
                                selectedEvents.filter(
                                    (event) =>
                                        event.name === name &&
                                        event.localDate == localDate &&
                                        event.localTime == localTime &&
                                        event.venueAddress == venueAddress
                                ).length !== 0
                            }
                        />
                        <List.Item.Meta
                            title={name}
                            description={`${venueAddress} | ${formatDate(
                                localDate
                            )}`}
                        />
                    </>
                </List.Item>
            )}
        />
    );
};

export default EventsList;
