import { AutoComplete, Typography, Button } from "antd";
import React, { useState } from "react";
import { User } from "../common/types";
import axios from "axios";

type UserSearchProps = {
    title?: string;
    placeholder: string;
    users: User[];
    onSelect?: (user: User) => void;
};

const { Title } = Typography;
var querystring = require('querystring');

const UserSearch: React.FC<UserSearchProps> = ({
    title,
    placeholder,
    users,
    onSelect,
}) => {
    const [query, setQuery] = useState<string>("");
    const submit = () => {
        if (query){
            console.log(query);
            const username = localStorage.getItem("username");
            const user = query;
            const data = {"username": username, "user": user, "action": "add"};
            axios.post(process.env.REACT_APP_HOST_API_URL + "/api/setting",
            querystring.stringify(data),
            {
                headers: { 
                "Content-Type": "application/x-www-form-urlencoded"
                }
            }).then( res => {
                console.log(res.data);
                window.location.reload();
            }).catch(function (error) {
                console.log(error.response.status) 
                console.log(error.response)
                console.log(error)
            });
        }
      };
    return (
        <div style={{ padding: 12 }}>
            <Title>{title}</Title>
            <AutoComplete
                options={users
                    .filter((user) => !user.blocked)
                    .filter((user) =>
                        user.name.toLowerCase().includes(query.toLowerCase())
                    )
                    .map((user) => ({
                        value: user.name,
                        label: user.name,
                        user,
                    }))}
                style={{ width: 200 }}
                placeholder={placeholder}
                value={query}
                onChange={(newQ) => setQuery(newQ)}
                onSelect={(_, option) => onSelect?.((option as any).user)}
            />
           <Button onClick={submit}> Add</Button>
           <br/>
           <Button onClick={submit}> Send</Button>
           {"\n"}
           <Button onClick={submit}> Save as Draft</Button>
        </div>
    );
};

export default UserSearch;
