import React from "react";
import { Typography, Table, Button, Slider } from "antd";
import CSS from "csstype";
import { firstButtonStyle, columns, dataSource } from "../../common/invitees";

const { Title } = Typography;

const UnfinalizedView = () => {
    return (
        <div>
            <Event />
            <Button type="primary" style={firstButtonStyle}>
                Submit
            </Button>
            <Button type="default">Save Draft</Button>
        </div>
    );
};

const Event = () => (
    <div style={eventStyle}>
        <Title level={3}>Event Name</Title>
        <Title level={4}>Event Info</Title>
        <Title level={4}>Invitees</Title>
        <Table dataSource={dataSource} columns={columns} />
        <Button type="primary" style={firstButtonStyle}>
            Yes
        </Button>
        <Button type="default">No</Button>
        <br />
        <Title style={marginTopStyle} level={5}>
            How excited are you?
        </Title>
        <Slider style={widthStyle} min={1} max={5} defaultValue={3} />
    </div>
);

const eventStyle: CSS.Properties = {
    marginLeft: "5em",
    padding: "2em",
    border: "1px solid rgba(0,0,0,0.1)",
    borderRadius: "4px",
    marginBottom: "2em",
};

const marginTopStyle: CSS.Properties = {
    marginTop: "1.5em",
};

const widthStyle: CSS.Properties = {
    width: "300px",
};

export default UnfinalizedView;
