import React, { useState } from "react";
import "react-big-calendar/lib/sass/styles.scss";
import { Calendar, momentLocalizer } from "react-big-calendar";
import moment from "moment";
import CSS from "csstype";
import { BackendEvent, CalendarEvent } from "../common/types";
import { defaultFilterState } from "../common/filter";
import Filters from "../components/Filters";

const localizer = momentLocalizer(moment);

const containerStyle: CSS.Properties = {
    height: "85vh",
    marginTop: "4vh",
    width: "90vw",
    marginLeft: "5vw",
    marginBottom: "12vh",
};

const backendEvents: BackendEvent[] = [
    {
        event: "JBConcert",
        startTime: "11-4-2021 15:00:00",
        endTime: "11-4-2021 15:45:00",
    },
];

const eventMapper = (events: BackendEvent[]): CalendarEvent[] => {
    return events.map((event, index) => ({
        index,
        title: event.event,
        start: new Date(event.startTime),
        end: new Date(event.endTime),
    }));
};

const AvailabilityPage: React.FC = () => {
    const [filterState, setFilterState] = useState(defaultFilterState);
    return (
        <div style={containerStyle}>
            <Filters
                filterState={filterState}
                setFilterState={setFilterState}
            />

            <Calendar
                defaultDate={new Date()}
                views={["month", "week", "day"]}
                localizer={localizer}
                events={eventMapper(backendEvents)}
            ></Calendar>
        </div>
    );
};

export default AvailabilityPage;
